.body3 {
  background: radial-gradient(ellipse at bottom, #11196b 0%, #1b6790 100%); }

.mainCon3 {
  display: flex;
  height: 100vh; }
  .mainCon3 .moon {
    position: absolute;
    left: 24%;
    bottom: 43%;
    transform: translateY(-50%);
    z-index: -1; }
    @media (min-width: 1440px) {
      .mainCon3 .moon {
        left: 31%;
        bottom: 48%; } }
    @media (min-width: 1920px) {
      .mainCon3 .moon {
        left: 27%;
        bottom: 48%; } }
    @media (max-width: 768px) {
      .mainCon3 .moon {
        left: 13%;
        bottom: 40%; } }
    @media (max-width: 480px) {
      .mainCon3 .moon {
        left: 2%;
        bottom: 40%; } }
    .mainCon3 .moon img {
      width: 90%;
      animation: moonLight 3s ease-in-out infinite alternate; }
  .mainCon3 .home {
    margin-top: auto;
    z-index: 1;
    position: relative; }
    .mainCon3 .home .pig {
      display: none; }
      @media (min-width: 992px) {
        .mainCon3 .home .pig {
          display: block; } }
    .mainCon3 .home .sm {
      display: none; }
      @media (max-width: 991px) {
        .mainCon3 .home .sm {
          display: block; } }
    .mainCon3 .home img {
      width: 100vw; }
      @media (max-width: 480px) {
        .mainCon3 .home img {
          height: 240px; } }
    .mainCon3 .home div {
      position: absolute; }
    .mainCon3 .home .light-right,
    .mainCon3 .home .light-left {
      position: absolute;
      top: 72px;
      width: max-content;
      animation: officeLight 2s ease-in-out infinite alternate; }
      @media (min-width: 1605px) {
        .mainCon3 .home .light-right,
        .mainCon3 .home .light-left {
          top: 86px; } }
      @media (min-width: 1920px) {
        .mainCon3 .home .light-right,
        .mainCon3 .home .light-left {
          top: 108px; } }
      @media (max-width: 1024px) {
        .mainCon3 .home .light-right,
        .mainCon3 .home .light-left {
          top: 60px; } }
      @media (max-width: 991px) {
        .mainCon3 .home .light-right,
        .mainCon3 .home .light-left {
          display: none; } }
      .mainCon3 .home .light-right img,
      .mainCon3 .home .light-left img {
        width: 100%; }
        @media (max-width: 1024px) {
          .mainCon3 .home .light-right img,
          .mainCon3 .home .light-left img {
            width: 70%; } }
        @media (max-width: 480px) {
          .mainCon3 .home .light-right img,
          .mainCon3 .home .light-left img {
            width: 20%; } }
      @media (max-width: 892px) {
        .mainCon3 .home .light-right,
        .mainCon3 .home .light-left {
          display: none; } }
    .mainCon3 .home .light-right {
      right: 5.5%; }
      @media (min-width: 1440px) {
        .mainCon3 .home .light-right {
          right: 6.5%; } }
      @media (min-width: 1605px) {
        .mainCon3 .home .light-right {
          right: 8.5%; } }
      @media (min-width: 1920px) {
        .mainCon3 .home .light-right {
          right: 11.5%; } }
      @media (max-width: 1024px) {
        .mainCon3 .home .light-right {
          right: -4.5%; } }
    .mainCon3 .home .light-left {
      left: 14.8%; }
      @media (min-width: 1440px) {
        .mainCon3 .home .light-left {
          left: 15.8%; } }
      @media (min-width: 1605px) {
        .mainCon3 .home .light-left {
          left: 17.9%; } }
      @media (min-width: 1920px) {
        .mainCon3 .home .light-left {
          left: 20.9%; } }
      @media (max-width: 1024px) {
        .mainCon3 .home .light-left {
          left: 15.8%; } }
    .mainCon3 .home .titel {
      text-transform: uppercase;
      position: absolute;
      font-size: 25px;
      text-shadow: 0 0 0px rgba(34, 34, 34, 0.25);
      color: #f4fdff;
      right: 13.5%;
      top: 0;
      animation: textLight 2s ease-in-out infinite alternate;
      pointer-events: none; }
      @media (min-width: 1920px) {
        .mainCon3 .home .titel {
          font-size: 35px; } }
      @media (max-width: 1024px) {
        .mainCon3 .home .titel {
          font-size: 18px; } }
      @media (max-width: 991px) {
        .mainCon3 .home .titel {
          display: none; } }
  .mainCon3 .employee-3 {
    position: absolute;
    z-index: 22;
    width: max-content;
    height: max-content;
    right: 31%;
    bottom: 0; }
    @media (min-width: 1920px) {
      .mainCon3 .employee-3 {
        right: 36%; } }
    @media (max-width: 1024px) {
      .mainCon3 .employee-3 {
        right: 23%; }
        .mainCon3 .employee-3 img {
          width: 80%; } }
    @media (max-width: 991px) {
      .mainCon3 .employee-3 {
        right: 28%; }
        .mainCon3 .employee-3 img {
          width: 70%; } }
    @media (max-width: 480px) {
      .mainCon3 .employee-3 {
        right: 23%; }
        .mainCon3 .employee-3 img {
          width: 60%; } }
    .mainCon3 .employee-3_1 {
      z-index: 2;
      position: relative; }
    .mainCon3 .employee-3_2 {
      position: absolute;
      top: 8%;
      right: -34%;
      z-index: 1;
      animation: employee-3_2 2s ease-in-out infinite alternate; }
      @media (max-width: 1024px) {
        .mainCon3 .employee-3_2 {
          right: -22%; } }
      @media (max-width: 991px) {
        .mainCon3 .employee-3_2 {
          right: -17%; } }
      @media (max-width: 480px) {
        .mainCon3 .employee-3_2 {
          right: -9%; } }
    .mainCon3 .employee-3_3 {
      position: absolute;
      top: 8%;
      left: -25%;
      z-index: 1;
      animation: employee-3_3 2s ease-in-out infinite alternate; }
      @media (max-width: 1024px) {
        .mainCon3 .employee-3_3 {
          left: -19%; } }
      @media (max-width: 991px) {
        .mainCon3 .employee-3_3 {
          left: -17%; } }
      @media (max-width: 480px) {
        .mainCon3 .employee-3_3 {
          left: -15%;
          top: 7.5%; } }

.road {
  position: absolute;
  bottom: 0;
  z-index: 4; }

@keyframes moonLight {
  0% {
    filter: drop-shadow(0 0 0px #fff); }
  100% {
    filter: drop-shadow(0 0 30px #fff); } }

@keyframes textLight {
  0% {
    text-shadow: 0 0px 0px #fff; }
  100% {
    text-shadow: 0 0px 20px #fff; } }

@keyframes employee-3_2 {
  0% {
    transform: translate(5px, 8px) rotate(9deg); }
  100% {
    transform: translate(0, 0) rotate(0); } }

@keyframes employee-3_3 {
  0% {
    transform: translate(-6px, 7px) rotate(-12deg); }
  100% {
    transform: translate(0, 0) rotate(0); } }

@media (max-width: 991px) {
  @keyframes employee-3_3 {
    0% {
      transform: translate(-6px, 4px) rotate(-12deg); }
    100% {
      transform: translate(0, 0) rotate(0); } } }

